.more-intro-home-page {
    padding: rem(160px) 0;

    .intro-img {
        width: 540px;
        height: 600px;
        background-image: url(../../img/homepage/family-gathering-desktop.jpg);
    }

    button {
        margin-bottom: rem(78px);
    }

    @include for-mobile-layout {
        flex-direction: column;
        text-align: center;

        .intro-img {
            width: 326px;
            height: 400px;
            background-image: url(../../img/homepage/family-gathering-mobile.jpg);
        }
    }
}